.list a:hover {
  opacity: 1; }
  .list a:hover li div.hero_image img {
    opacity: 0.8;
    transition: opacity 0.3s ease; }
  .list a:hover li div.blog__info h2,
  .list a:hover li div.blog__info h3,
  .list a:hover li div.blog__info p {
    transform: translateX(10px);
    transition: transform 0.5s ease-out; }

.list .hero_image {
  width: 100%;
  height: 33vh;
  overflow: hidden;
  background-color: #000; }
  .list .hero_image img {
    object-fit: cover;
    object-position: 50% 50%;
    opacity: 1;
    transition: opacity 0.3s ease;
    min-height: 100%; }

.list .blog__info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 1.5rem 1.25rem;
  transform: translateX(0px);
  transition: transform 0.3s ease-in;
  border-bottom: 1px solid #ebebeb; }
  .list .blog__info h2,
  .list .blog__info h3,
  .list .blog__info p {
    transform: translateX(0px);
    transition: transform 0.5s ease-out; }

.list li {
  opacity: inherit;
  display: flex;
  justify-content: center;
  flex-direction: column;
  min-height: 38vh;
  margin-bottom: 0; }

.list h2 {
  margin-bottom: 0.5rem; }

.list h3 {
  margin-bottom: 1rem; }

.list p {
  max-width: 900px; }

@media (min-width: 768px) {
  .list li {
    min-height: 250px;
    height: 33.333vh;
    flex-direction: row; }
  .list .hero_image {
    height: 100%; }
    .list .hero_image img {
      min-width: 100%;
      height: 100%;
      width: auto;
      min-height: 0; }
  .list .blog__info {
    min-width: 70%; } }

@media (min-width: 1280px) {
  .list .blog__info {
    padding: 3rem; }
  .list h3 {
    margin-bottom: 1.2rem; } }
